<template>
  <nav class="navbar navbar-default">
    <!-- Brand and toggle get grouped for better mobile display -->
    <div class="navbar-header">
      <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#nav-icon-collapse" aria-expanded="false">
        <span class="sr-only">Toggle navigation</span>
        <span class="icon-bar" v-for="index in 3" :key="index"></span>
      </button>
      <!-- logo -->
      <a class="logo" href="#" data-scroll-nav="0">
        <img src="@/assets/img/logo/logo.svg" alt="Logo Alexis LAFRANCE" id='logo' height="1" width="1">
      </a>
    </div>

    <!-- Collect the nav links, and other content for toggling -->
    <div class="collapse navbar-collapse" id="nav-icon-collapse">
      <!-- links -->
      <ul class="nav navbar-nav navbar-right">
        <li v-for="(link, index) in links" :key="index">
          <a href="#" v-bind:data-scroll-nav="index" v-bind:class="(!index) ? 'active' : ''">{{ link}}</a>
        </li>
      </ul>
    </div><!-- /.navbar-collapse -->
  </nav>
</template>

<script>

import $ from "jquery";
// ADD CSS TO NAVBAR WHEN THE USER CLICK ON THE HAMBURGER

// BOOLEAN PARAMETER

export default {
  name: "Header",
  data: function () {
    return {
      links: ["Accueil", "A propos de moi", "Portfolio", "Contact"],
    }
  },
  mounted() {
    /* ----------------------------------------------------------------
                        JQUERY DOM
    ---------------------------------------------------------------- */
    const navbarBox = $("#nav-icon-collapse");
    const hamburger = $("button.navbar-toggle");
    const navbar =  $("ul.nav");

    /* ----------------------------------------------------------------
                    FUNCTION UTILITIES (ADD/REMOVE CLASS)
    ---------------------------------------------------------------- */
    function change_navbar_scroll(add_class_scroll) {
      const navbar = $(".navbar-default");
      if (add_class_scroll) {
        navbar.addClass("nav-scroll");
        $("img#logo").css("filter", "invert(0%) sepia(4%) saturate(7500%) hue-rotate(3deg) brightness(104%) contrast(101%)");
      }
      else {
        navbar.removeClass("nav-scroll");
        $("img#logo").removeAttr("style");
      }
    }

    function resetNavbar() {
      const bodyScroll = $(window).scrollTop(), h_hight = $(".hero").outerHeight();
      if (bodyScroll < h_hight / 2)
        change_navbar_scroll(false);
      $('html, body').css({
        overflow: 'auto',
      });
      hamburger.attr('aria-expanded', "false");
    }

    /* ----------------------------------------------------------------
                                CLICK EVENT
    ---------------------------------------------------------------- */
    hamburger.click(function () {
      if (hamburger.attr('aria-expanded') === "false") {
        navbarBox.addClass("navbar-phone");
        navbar.removeClass("navbar-nav");
        change_navbar_scroll(true);
        $('html, body').css({
          overflow: 'hidden',
        });
        hamburger.attr('aria-expanded', "true");
      }
      else
        resetNavbar();
    })
    navbar.click(function () {
      resetNavbar();
    })

    /* ----------------------------------------------------------------
                            NAVBAR SCROLLING
    ---------------------------------------------------------------- */
    // close navbar-collapse when a  clicked
    $(".nav a").on('click', function () {
      $(".navbar-collapse").removeClass("in").addClass("collapse");
    });
    // navbar scrolling background
    $(window).on("scroll",function () {
      const bodyScroll = $(window).scrollTop(), h_hight = $(".hero").outerHeight();
      if (bodyScroll > h_hight / 2)
        change_navbar_scroll(true);
      else
        change_navbar_scroll(false);
    });
  },
}
</script>

